import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi } from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-book.component.html'
})
export class EditBookComponent {
	@ViewChild('name') name: NgModel;
	@ViewChild('priority') priority: NgModel;
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	bookName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	description: any = "";
	image: any;
	imgErrDisplay: boolean = false;
	loading: boolean = false;
	imgErr: any = '';
	data: any;
	allData: any = [];
	imgName:any;
	bookPriority:Number=0;
	bookImage:any;
	loginFlagToken:any;
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"id":this.id
		}
		this.books.getBooks(dataArrObj).subscribe(data=>{
			this.loading = false;
			if(data){
				let result = data.result[0];
				this.bookName = result.name;
				this.description = result.description;
				this.bookPriority = result.priority;
				if(result.logo_image){
					this.bookImage = result.logo_image+'?access_token='+this.loginFlagToken;
				}
			}
		},
		err=>{
			console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["books"]);
	}

	async submit() {
		this.submitted = true;
		if (this.name.valid && this.priority.valid) {
			let uploadFlag1: any = true;
			//upload logo image
			if (this.allData != "" && this.allData != undefined) {
				uploadFlag1 = await this.uploadImage(this.allData[0]);
			}

			//function call to change base64 format data to Image
			var dataArrObj = {
				"name": this.bookName,
				"description": this.description,
				"logo_image": this.imgName,
				"priority": this.bookPriority
			}
			this.loading = true;
			this.books.addEditBooks(dataArrObj, this.id).subscribe(
				data=>{
					this.loading = false;
					if(data){
						console.log(data);
						if(data.result.id > 0){
							this.result = 'success';
						}
						this.display=true;
					}else{
						this.display=false;
						this.result = 'Something went wrong.';
					}
				},
				err=>{
					this.loading = false;
					console.log("err", err);
				},
				()=>{

				}
			)
		}
	}

	onFileChange(file) {
		console.log("file", file);
		this.imgErrDisplay = false;
		this.data = new FormData();
		var imageData = new FileReader();
		if (file.target.files[0]) {
			var fileSize = Math.round((file.target.files[0].size) / 1024);
			var imgExt = file.target.files[0].name.split(".");
			console.log("file.target.files[0].type",file.target.files[0].type);
			if (imgExt.length > 2) {
				this.imgErr = "Invalid Image Format / Image name";
				this.imgErrDisplay = true;
			} else if (((file.target.files[0].type) != "image/jpg") && ((file.target.files[0].type) != "image/png") && ((file.target.files[0].type) != "image/jpeg")) {
				this.imgErr = "Invalid Image Format";
				this.imgErrDisplay = true;
			} else if (fileSize > 7000) {//chk img size
				this.imgErr = "Invalid Image Size";
				this.imgErrDisplay = true;
			} else {
				imageData.onload = (event: any) => {
					var img = new Image();
					img.src = event.target.result;
					if (img.width > 500 || img.height > 500) {
						this.imgErr = "Invalid Image Dimension. It needs to be of max 500*500";
						this.imgErrDisplay = true;
					} else {
						let targetFile = file.target.files[0];
						let fileExtension = '.' + targetFile['name'].split('.').pop();
						let filename = Math.random().toString(36).substring(7) + new Date().getTime() + fileExtension;
						this.data.append("file", file.target.files[0], filename);
						this.allData.push(this.data);
					}
				}
				imageData.readAsDataURL(file.target.files[0]);
			}
		}
	}

	uploadImage(fdata) {
		return new Promise((resolve, reject) => {
			let fileUrl = hostname+"/api/containers/logo_image";
			//let fileUrl = "http://phapi.experiencecommerce.com:4500/api/containers/logo_image";
			this.loading = true;
			this.http.post(fileUrl + '/upload?access_token='+this.loginFlagToken, fdata).subscribe(res => {
				var dataimg = res['_body'];
				dataimg = JSON.parse(dataimg);
				if(dataimg){
					let imgName = dataimg['result']['files']['file'][0]['name'];
					this.imgName = fileUrl+"/download/"+imgName;
					resolve(true);
				}else{
					resolve(false);
				}
			},err => {
				this.imgErr = "Image upload failed";
				//this.imgshow = this.defaultImg;
				this.imgErrDisplay = true;
				this.loading = false;
				resolve(false);
			},
			() => {

			});
		});
	}
}


